@font-face {
  font-family: "SCoreDream";
  font-style: normal; 
  src: url(https://cdn.jsdelivr.net/gh/webfontworld/SCoreDream/SCoreDream1.woff2) format("woff2"),
       url(https://cdn.jsdelivr.net/gh/webfontworld/SCoreDream/SCoreDream1.woff) format("woff");
}

body {
  margin: 0;
}

a {
  text-decoration: none;
  color: #121212;
}

.container {
  background-image: url("./bg1.jpg");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  width: 100%;
  height: 100%;
  padding-top: 40px;
  padding-bottom: 40px;
}

.remaining_questions_container {
  position: fixed;
  bottom: 25px;
  left: 50%;
  transform: translate(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30%;
  height: 75px;
  background-color: rgba(250, 250, 250, 1);
  border-radius: 26px;
  box-shadow: 2px 2px 5px gray;
  font-family: "SCoreDream";
  font-weight: 800;
  font-size: 18px;
  color: #121212;
}
.remaining_questions_container:active{box-shadow: none}
.remaining_questions_text_color_red {color: #DD4A5D}
.remaining_questions_text_color_green {color: #8AC481}

.card_container {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 90%;
  height: 70vh;
  background-color: rgba(250, 250, 250, 0.8);
  border-radius: 26px;
  padding: 20px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;
  box-shadow: 2px 2px 5px gray;
}

.question_container {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.answer_container {
  display: flex;
  flex: 2;
  flex-direction: column;
  width: 60%;
}

.question_text {
  font-family: "SCoreDream";
  font-weight: 800;
  font-size: 24px;
  color: #121212;
}

.question_bold_text {
  font-family: "SCoreDream";
  font-weight: 900;
  font-size: 66px;
  margin-right: 14px;
  color: #121212;
}

.answer_button1 {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  background-color: rgba(245, 245, 245, 1);
  outline: none;
  border: none;
  border: rgba(158, 158, 158, 1) 0.1px solid;
  border-radius: 24px;
  font-family: "SCoreDream";
  font-weight: 800;
  font-size: 18px;
  color: #121212;
  margin-bottom: 14px;
}

.answer_button2 {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  background-color: rgba(245, 245, 245, 1);
  outline: none;
  border: none;
  border: rgba(158, 158, 158, 1) 0.01px solid;
  border-radius: 24px;
  font-family: "SCoreDream";
  font-weight: 800;
  font-size: 18px;
  color: #121212;
}

@media (max-width: 600px) {
  .remaining_questions_container {width: 75%}
  .card_container {width: 80%;height: 45vh}
  .answer_container {width: 80%}
  .question_text {font-size: 18px}
  .question_bold_text {font-size: 36px;margin-right: 8px}
  .answer_button1 {font-size: 14px} 
  .answer_button2 {font-size: 14px}
}
