@font-face {
  font-family: "SCoreDream";
  font-style: normal; 
  src: url(https://cdn.jsdelivr.net/gh/webfontworld/SCoreDream/SCoreDream1.woff2) format("woff2"),
       url(https://cdn.jsdelivr.net/gh/webfontworld/SCoreDream/SCoreDream1.woff) format("woff");
}

@font-face {
  font-family: 'RixInooAriDuriR';
  font-style: normal;
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2207-01@1.0/RixInooAriDuriR.woff2') format('woff2');
}

body {
  margin: 0;
}

a {
  text-decoration: none;
  color: #121212;
}

.container {
  background-image: url("./bg1.jpg");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  width: 100%;
  height: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
}

.card_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 85%;
  min-height: calc(100vh - 150px);
  background-color: rgba(235, 235, 235, 0.75);
  border-radius: 32px;
  box-shadow: 2px 2px 5px gray;
  padding-top: 25px;
  padding-bottom: 25px;
  margin-left: auto;
  margin-right: auto;
}

.card_text {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "SCoreDream";
  font-weight: 900;
  font-size: 24px;
  color: #121212;
  margin-bottom: 28px;
}

.card_image_container {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card_image {
  width: 150px;
  height: 150px;
  margin-bottom: 12px;
}

.card_image_text {
  font-family: 'RixInooAriDuriR';
  font-weight: nomal;
  font-size: 28px;
  color: #121212;
}

.card_explanation_container {
  display: flex;
  flex-direction: column;
  flex: 1;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.card_explanation_text {
  font-family: "SCoreDream";
  font-weight: 900;
  font-size: 12px;
  text-align: center;
  white-space: pre-wrap;
  color: #121212;
  padding-left: 8px;
  padding-right: 8px;
}

.card_compatibility_container {
  display: flex;
  flex-direction: row;
  flex: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.card_compatibility_flex_container {
  display: flex;
  flex-direction: column;
  flex: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.card_compatibility_best_main_text {
  font-family: "SCoreDream";
  font-weight: 900;
  font-size: 18px;
  text-align: center;
  white-space: pre-wrap;
  color: rgb(118, 176, 109);
  margin-bottom: 8px;
}

.card_compatibility_worst_main_text {
  font-family: "SCoreDream";
  font-weight: 900;
  font-size: 18px;
  text-align: center;
  white-space: pre-wrap;
  color: rgb(201, 54, 73);
  margin-bottom: 8px;
}

.card_compatibility_sub_text {
  font-family: "SCoreDream";
  font-weight: 900;
  font-size: 14px;
  text-align: center;
  white-space: pre-wrap;
  color: #121212;
}

.bottombar_container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  margin-top: 70px;
}

.again_container {
  display: flex;
  flex: 1;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  min-width: 300px;
  min-height: 100px;
  background-color: rgba(235, 235, 235, 0.75);
  border: #121212 solid 0.5px;
  border-radius: 32px;
  box-shadow: 2px 2px 5px gray;
  margin-bottom: 20px;
}

.again_image {
  width: 34px;
  height: 34px;
  margin-right: 16px;
}

.again_text {
  font-family: "SCoreDream";
  font-weight: 900;
  font-size: 1.35em;
}
