@font-face {
  font-family: "SCoreDream";
  font-style: normal; 
  src: url(https://cdn.jsdelivr.net/gh/webfontworld/SCoreDream/SCoreDream1.woff2) format("woff2"),
       url(https://cdn.jsdelivr.net/gh/webfontworld/SCoreDream/SCoreDream1.woff) format("woff");
}

@font-face {
  font-family: 'RixInooAriDuriR';
  font-style: normal;
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2207-01@1.0/RixInooAriDuriR.woff2') format('woff2');
}

body {
  margin: 0;
}

a {
  text-decoration: none;
  color: #121212;
}

.container {
  background-image: url("./bg1.jpg");
  background-size: 100vw 100vh;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
  width: 100vw;
  height: 100vh;
}

.card_image_container {
  display: flex;
  flex: 1;
  width: 75%;
}

.card_image_position {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card_image {
  width: 130px;
  height: 130px;
  margin-bottom: 24px;
}

.card_image_text {
  font-family: 'RixInooAriDuriR';
  font-weight: nomal;
  font-size: 28px;
  color: #121212;
}

.card_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 85%;
  height: 75%;
  background-color: rgba(235, 235, 235, 0.75);
  border-radius: 32px;
  box-shadow: 2px 2px 5px gray;
}

.card_text {
  display: flex;
  flex-direction: column;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.card_main_text {
  font-family: 'RixInooAriDuriR';
  font-weight: nomal;
  font-size: 42px;
  text-align: center;
  color: #121212;
  margin-bottom: 8px;
}

.card_sub_text {
  font-family: "SCoreDream";
  font-weight: 900;
  font-size: 16px;
  color: #121212;
}

.text_color {margin-right: 10px}
.text_color1 {color: #DD4A5D; margin-right: 2px}
.text_color2 {color: #F8E781; margin-right: 2px}
.text_color3 {color: #8AC481; margin-right: 1px}
.text_color4 {color: #6EC5D5}

.card_button {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.card_button_text {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60%;
  height: 30%;
  background-color: rgba(235, 235, 235, 1);
  font-family: "SCoreDream";
  font-weight: 900;
  font-size: 22px;
  color: #121212;
  border: #121212 2px solid;
  border-radius: 150px;
  box-shadow: 2px 2px 5px gray;
}
.card_button_text:active{box-shadow: none}
